header {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    a {
      display: inline-block;
      margin-left: 15px;
    }

    nav {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      .icon-container {
        margin-left: 15px;

        svg {
          width: 25px;
        }
      }
    }
  }
}